import _ from 'lodash';

   export function isAlphabets(text){
        const tmpText = _.toArray(text);
        const result = _.map(tmpText, (it)=>{
            if(_.toUpper(it) === _.toLower(it)){
                console.log('Text contains numbers');
                return false;
            }
        })

        return !(_.includes(result, false));
    }

   export function isNumbers(text){
        const tmpText = _.toArray(text);
        const result = _.map(tmpText, (it)=>{
            if(!(_.toUpper(it) === _.toLower(it))){
                console.log('Text contains alphabets');
                return false;
            }
        })

        return !(_.includes(result, false));
    }

    export function isMSISDN(text){
        if(text.length !== 10){
            return false;
        }

        const tmpText = _.toArray(text);
        const result = _.map(tmpText, (it)=>{
            if(!(_.toUpper(it) === _.toLower(it))){
                console.log('Text contains alphabets');
                return false;
            }
        })

        return !(_.includes(result, false));
    }

    export function isEmail(email){
        const re = /\S+@\S+\.\S+/;
        return re.test(email);
    }

    export function redirect(link){
        this.props.history.push(link);
    }



